body {
  background-color: white;
}

html {
  scroll-behavior: smooth;
}

.header {
  width: 100%;
  display: flex;
  justify-content: center;
  color: #353535;
  padding-top: 20px;
  font-family: fa5-proxima-nova, "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 30px;
  margin-bottom: 10px;
}

.categoryHeaders {
  color: white;
  font-family: fa5-proxima-nova, "Helvetica Neue", Helvetica, Arial, sans-serif;
}

#techniques {
  font-size: 35px;
}

#mainHeader {
  color: #353535;
  font-size: 60px;
}

#description {
  color: #353535;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  flex-direction: column;
  align-items: center;
}

#inContain {
  margin: 100px 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100px;
}

.input {
  margin: 10px 0;
  background-color: #fff9f9;
  color: #353535;
  font-weight: bold;
}

#addTechnique {
  background-color: #284b63;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
}

.titleIn {
  min-height: 30px;
  border-radius: 5px;
  font-size: 20px;
  font-family: fa5-proxima-nova, "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.titles {
  font-family: fa5-proxima-nova, "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #284b63;
}

.videoFrame {
  margin: 10px 0px 10px 0px;
  max-height: 180px;
  max-width: 250px;
}

@media (max-width: 500px) {
  .videoFrame {
   
    max-width: 230px;
  }
  
}

#addCategory {
  background-color: #284b63;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
}

#categoryKeysTitle {
  font-family: fa5-proxima-nova, "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #353535;
  font-size: 25px;
}

#addNote:focus {
  border: 2px solid white;
}

#bjjLogsCon {
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
 
}

#techniqueSection {
  border: 2px solid #284b63;
  margin-bottom: 20px;
  width: 90%;
  margin-left: 5%;
}

#categorySection {
  border: 2px solid #284b63;
  width: 90%;
  margin-left: 5%;
}

#techniqueContain {
  display: flex;
  justify-content: center;
}

#categorizedLogsCon {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.log {
  width: 250px;
  height: auto;
  border-radius: 10px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  margin: 20px;
  font-family: fa5-proxima-nova, "Helvetica Neue", Helvetica, Arial, sans-serif;
  cursor: pointer;
  touch-action:none;
  position:relative;
  z-index:3;
}

@media (max-width: 600px) {

.catLog {
  width:250px;
}

}

.category {
  width: 60%;
  border-radius: 10px;
  border: 5px solid slateblue;
  padding: 10px;
  margin: 20px 0px 20px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  z-index:2;
}

.categoryButtons {
  background-color: rgb(155, 65, 65);
  border: none;
  padding: 10px;
  border-radius: 5px;
  color: white;
  font-size: 25px;
  font-weight: bold;
  cursor: pointer;
}

#categoryButtons:hover {
  background-color: rgb(131, 29, 29);
  cursor: pointer;
}

#categoryKeyContain {
  position: relative;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  
}

.center {
  display: flex;
  justify-content: center;
}

.categoryKeys {
  margin: 10px 10px 10px 10px;
  width:250px;
  white-space:normal;
  border-radius: 5px;
  text-align: center;
  border: 3px solid slateblue;
  color: white;
  font-size:40px;
  font-weight:bold;
  line-height:50px;
  font-family: fa5-proxima-nova, "Helvetica Neue", Helvetica, Arial, sans-serif;

}

#categoriesIn {
  margin-top: 200px;
}

.dragging {
  opacity: 0.7;
  
}

#categoriesCon {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h4 {
  color: rgb(255, 255, 255);
  font-size: 20px;
}

label {
  font-family: fa5-proxima-nova, "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #353535;
  font-size: 25px;
  font-weight: bold;
}

.saveCancel {
  display: none;
  margin: 20px;
}

.noteContain {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 250px;
  margin: 10px 0px 10px 0px;
}

.notesIn {
  display: none;
}
.noteModify {
  font-size: 15px;
  font-weight: bold;
  background-color: white;
  border: 1px solid black;
  margin: 2px 0px 2px 0px;
  border-radius: 4px;
}

.notesOut {
  text-align: center;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 250px;
  overflow-wrap: break-word;
  word-wrap: break-word;
  hyphens: auto;
}

#add {
  margin: 30px 0px 30px 0px;
  background-color: #0ead69;
  border: none;
  padding: 10px;
  border-radius: 5px;
  color: white;
  font-size: 25px;
  font-weight: bold;
  width: 50px;
  justify-content: center;
}

#add:hover {
  background-color: #0f8853;
  cursor: pointer;
}

#delete {
  margin: 20px 0px 20px 0px;
  background-color: rgb(155, 65, 65);
  border: none;
  padding: 10px;
  border-radius: 5px;
  color: white;
  font-size: 25px;
  font-weight: bold;
  cursor: pointer;
}

#delete:hover {
  background-color: rgb(131, 29, 29);
  cursor: pointer;
}

.buttonCon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#add:focus {
  border: 2px solid white;
}

#delete:focus {
  border: 2px solid white;
}

.display-none {
  display: none;
}

.display-block {
  display: block;
}

.videoContain {
  flex-direction: column;
  align-items: center;
}